import React from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";
import { Aboutdropdown, Complementry, Regular } from "../../Data/NavbarData";
function Navbar() {
    const [open, setopen]=useState(false);
  return (
    <div  className="container-fluids sticky-top mx-3 ">
      <div className="row">
        <div style={{backgroundColor:"#1515ad"}} className="col-12  shadow ">
          <nav className=" navbar ">
            <a href="/#"> Navbar</a>
            <i  onClick={()=>(setopen(!open))} class="uil toggle uil-align-justify"></i>
            <ul className= {`${open===true?" line-content":"close m-0  line-content"}`} >
              <li>
                <a href="/tutorweb/"> Home </a>
              </li>
            
              <li
                className=" dropdownn"
                style={{ fontWeight: "500" }}
              >
                <a href="/"> About Us </a>
                <i class="uil drop uil-angle-down"></i>

                <ul  className="dropdown-menubar rounded bg-primary">
                  {Aboutdropdown.map((data,key)=>(
                   <li  className="border-bottom about-li  d-flex justify-content-between text-capitalize">
                        <a className=" ms-0 rounded text-white" href="/">
                          {data.name}
                        </a>
                     <i class="uil down uil-angle-right"></i>
                      </li>
                      ))}
                </ul>
              </li>

              <li
                className=" dropdownn"
                style={{ fontWeight: "500" }}
              >
                Our Courses
                <i class="uil drop uil-angle-down"></i>

                <ul  className="dropdown-menubar   rounded bg-primary">
                  <div className="row  row-cols-md-2">
                    <div className="regulor p-3">
                      <h3 className="text-uppercase">Regular Courses</h3>
                     {Regular.map((data,key)=>( <li  className="sub-dropdown text-capitalize">
                      < span className="list">{data.list}</span><i class="uil down uil-angle-down"></i>
                        <a className="sublist ms-5 rounded text-white" href="/">
                          {data.anker}
                        </a>
                      </li>
                      ))}
                    </div>
                    <div className="complementry p-3" style={{borderLeft:"1px solid white"}}>
                      <h3 className="text-uppercase">Complementary Courses</h3>
                     {Complementry.map((data,key)=>( <li className="sub-dropdown  text-capitalize">
                       {data.list}<i class="uil down uil-angle-down"></i>
                      <a className="sublist ms-5 rounded text-white" href="/">
                          {data.anker}
                        </a>
                      </li>
                      ))}
                    </div>
                  </div>
                </ul>
              </li>
              <li>
                <a href="/tutorweb/faq"> FAQ</a>
              </li>
              <li>
                <a href="/contact"> Contact Us</a>
              </li>
              <div className="d-flex btns" style={{ alignItems: "center" }}>
                <Link
                  to="/contact"
                  className="btn btn-outline-warning nav-btn me-2 font-weight-bold"
                  type="submit"
                >
                  <b> Class Join Now</b>
                </Link>
                <Link
                  to="/contact"
                  className="btn nav-btn trial btn-outline-light"
                  type="submit"
                >
                  <b> Book for Free Trial</b>
                </Link>
              </div>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
