import React from "react";
import "./Batch.css";
import { Batchdata } from "./BatchData";
function BatchCard(props) {
  return (
    <div className="batch-card">
      <div className="box cardB rounded border px-0  py-3 px-md-4 shadow">
        <ol className="text-start  row row-cols-3 text-capitalize">
        <div className="mt-sm-3"> </div>
       <h3 className="mt-sm-3">Private</h3>
       <h3 className="mt-sm-3">solo</h3>
     {Batchdata.map((data,key)=>( <> <div className="mt-3 d-flex"> <i className="uil  d-none d-md-block uil-arrow-right"></i> {data.name}</div>
       <div className="mt-3">{data.private}</div>
       <div className="mt-3">{data.solo}</div>
       </>
       ))}
        </ol>
      </div>
    </div>
  );
}

export default BatchCard;
