import React from "react";
import "./Courses.css";
import { courseData } from "../../Data/NavbarData";
import img from "../../All_image/feature.jpg";
import { Link, useLocation } from "react-router-dom";
import { useState } from "react";
function Courses() {
  const [header ,setheader]=useState("shoyeb");
  console.log(useLocation.state);
  return (
    <>
      <div className="courses mt-5 ">
        <div className="container-fluid">
          <div className="row ">
            <div className="col-11 m-auto pb-4 bg-white shadow rounded">
            <h1
        className="text-center text-uppercase shadow py-2 m-0"
        style={{ backgroundColor: "#30b23022  ", fontSize: "50px" }}
      >
        our <span className="text-primary"> Course</span>
      </h1>
              <div className="course-cards">
                <div class=" d-flex cards mt-4 bg-white">
                {courseData.map((data,key)=>( 
                     <div className="col p-4 rounded border text-center  c-cards shadow bg-white">
                      <img src={data.image} alt="" style={{ height: "300px",width:"250px" }} />
                      <h4 className="course-name text-uppercase mt-2">{data.anker}</h4>
                  
                      <div><button className="btn  mt-2 trial btn-outline-warning">
                        Book a Demo class
                      </button></div>
                     <div> <Link to="/tutorweb/learn" state ={{header:header}} className=" btn mt-2 btn-success">Learn More</Link></div>
                    </div>
                    ))}
              </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Courses;
