import React from "react";
import { Faqdata } from "../../Data/FaqsData";

function Questions() {
  return (
    <div>
      <div class="accordion" id="accordionExample">
        {Faqdata.map((data,key)=>(
        <div class="cardl">
          <div class="card-header" id="headingOne">
            <h2 class="mb-0">
              <button style={{textDecoration:"none"}}
                class="btn btn-link collapsed"
                type="button"
                data-toggle="collapse"
                data-target={`#collapse${data.numb}`}
                aria-expanded="false"
                aria-controls={`collapse${data.numb}`}
              >
             {data.id}. {data.ques}? <i class="uil uil-angle-down"> </i>
              </button>
            </h2>
          </div>

          <div
            id={`collapse${data.numb}`}
            class="collapse"
            aria-labelledby='headingOne'
            data-parent="#accordionExample"
          >
            <div class="card-body">
             <p className="ms-5"> {data.ans} </p>
            </div>
          </div>
        </div>
        ))}
    </div>
    </div>
  );
}

export default Questions;
